html {
    overflow-x: hidden;
}

.wrapper {
    display: flex;
    flex-direction: column;
    background-image: url("img/dizajn.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.header {
    display: flex;
    height: 80px;
    background-color: rgb(26, 93, 145, 0.9);
}

.content {
    background-color: rgba(255, 0, 0, 0.15);
    min-height: calc(100vh - 130px);
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    box-shadow: inset 0px 0px 10px black;
}

.footer {
    height: 50px;
    background-color: rgba(26, 93, 145, 0.9);
}

.container {
    padding-top: 30px;
    padding-bottom: 50px;
    max-width: 1800px;
    margin: 0 auto;
}
