.main__selectionBOX {
    display: flex;
    justify-content: center;
}
.main__selection {
    width: 100px;
    display: flex;
    justify-content: space-between;
}
.main__rhomb1 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(218, 65, 28);
}
.main__rhomb2 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.main__rhomb3 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.main__rhomb4 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}

.main__row {
    display: flex;
    justify-content: space-around;

    animation-duration: 3s;
    animation-name: mainRow;
}
@keyframes mainRow {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.main__col{
    margin-top: 50px;
    margin-left: 10px;
    margin-right: 10px;
}

.main__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(0, 0, 0);
    text-align: center;
    margin-bottom: 10px;
}

.main__img {
max-width: 100%;
}

@media (max-width: 1023px) {
    .main__row {
        flex-wrap: wrap;
    }
}