.about__selectionBOX {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}
.about__selection {
    width: 100px;
    display: flex;
    justify-content: space-between;
}
.about__rhomb1 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.about__rhomb2 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.about__rhomb3 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(218, 65, 28);
}
.about__rhomb4 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}

.about__box {
    animation-duration: 3s;
    animation-name: aboutBox;
}
@keyframes aboutBox {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.about__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(0, 0, 0);
    text-align: center;
}

.about__row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
}
.about__col {
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 50px;
    border: 1px solid black;
    background-color: rgb(26, 93, 145, 0.9);
    color: white;
    border-radius: 15px;
    padding: 50px;
    width: 700px;
    font-size: 30px;
}

@media (max-width: 1023px) {
    .about__row {
        flex-wrap: wrap;
    }
}

@media (max-width: 600px){
    .about__col{
        padding: 20px;
    }
}