.contacts__selectionBOX {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}
.contacts__selection {
    width: 100px;
    display: flex;
    justify-content: space-between;
}
.contacts__rhomb1 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.contacts__rhomb2 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.contacts__rhomb3 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.contacts__rhomb4 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(218, 65, 28);
}

.contacts__box {
    animation-duration: 3s;
    animation-name: contactsBox;
}
@keyframes contactsBox {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.contacts__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(0, 0, 0);
    text-align: center;
}

.contacts__row {
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
}
.contacts__col {
    border: 1px solid black;
    padding: 50px;
    border-radius: 15px;
    display: flex;
    font-size: 30px;
    background-color: rgb(26, 93, 145, 0.9);
    color: white;
}

.contacts__text {
    margin-bottom: 35px;
}

.contacts__nameInput {
    height: 25px;
    width: 250px;
    border: 1px solid black;
}

.contacts__mail {
    margin-top: 15px;
}

.contacts__mailInput {
    height: 25px;
    width: 250px;
    border: 1px solid black;
}

.contacts__massage {
    margin-top: 15px;
    margin-bottom: 7px;
}

.contacts__massageInput {
    height: 50px;
    width: 700px;
    border: 1px solid black;
    margin-bottom: 35px;
}

.contacts__button {
    width: 180px;
    border: 1px solid black;
    background-color: white;
    box-shadow: 3px 3px 3px black;
    text-align: center;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 15px;
    padding: 10px;
    text-transform: uppercase;
    font-weight: 700;
    color: black;
}
.contacts__button:hover {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
}
.contacts__button:active {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 0px black;
    transform: translate(3px, 3px);
}

@media (max-width: 960px) {
    .contacts__massageInput {
        width: 250px;
    }
}

@media (max-width: 600px){
    .contacts__col{
        padding: 20px;
    }
}