.portfolio__selectionBOX {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}
.portfolio__selection {
    width: 100px;
    display: flex;
    justify-content: space-between;
}
.portfolio__rhomb1 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.portfolio__rhomb2 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(218, 65, 28);
}
.portfolio__rhomb3 {
    width: 10px;
    height: 10px;
    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}
.portfolio__rhomb4 {
    width: 10px;
