    border: 1px solid black;
    border-radius: 50%;
    background-color: rgb(26, 93, 145);
}

.portfolio__box {
    animation-duration: 3s;
    animation-name: portfolioBox;
}
@keyframes portfolioBox {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.portfolio__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(0, 0, 0);
    text-align: center;
}

.portfolio__row {
    margin-top: 25px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.portfolio__subTitle {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(0, 0, 0);
    text-align: center;
    text-align: center;
    margin-bottom: 25px;
}

.portfolio__imgBox {
    background-color: 1px solid black;
    padding: 50px;
    background-color: rgb(26, 93, 145, 0.9);
    border-radius: 15px;
    border: 1px solid black;
    max-width: 100%;
}

.portfolio__img {
    max-width: 100%;
    border: 1px solid black;
}
