.header__row {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.header__logo {
    padding-left: 10px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 10;
}
.header__logoImg {
    padding: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 50px;
    font-weight: 700;
    border: 1px solid black;
    background-color: white;
    border-radius: 15px;
    color: black;
    box-shadow: 3px 3px 3px black;

    animation-name: logoImg;
    animation-duration: 3s;
}
@keyframes logoImg {
    from {
        transform: translate(-100px, 0px);
    }
    to {
        transform: translate(0px, 0px);
    }
}

.header__logoImg:active {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 0px black;
    transform: translate(3px, 3px);
}

.header__logoText {
    padding-left: 5px;
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;

    animation-name: logoTxt;
    animation-duration: 3s;
}
@keyframes logoTxt {
    from {
        letter-spacing: 500px;
    }
    to {
        letter-spacing: 1px;
    }
}
@media (max-width: 320px) {
    .header__logoText {
        display: none;
    }
}

.header__menu {
    display: flex;
}
.header__menu li {
    width: 180px;
    border: 1px solid black;
    background-color: white;
    box-shadow: 3px 3px 3px black;
    text-align: center;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 10px;
    border-radius: 15px;
    padding: 10px;
    text-transform: uppercase;
    font-weight: 700;
    color: black;
}

.header__menu li:active {
    background-color: rgb(218, 65, 28);
    color: rgb(255, 255, 255);
    box-shadow: 0px 0px 0px black;
    transform: translate(3px, 3px);
}
.burger__menu {
    display: none;
}

@media (max-width: 960px) {
    /*--------меню бургер---------*/
    .header__menu {
        display: none; /*скрывает обычное меню*/
    }
    .burger-box{
        height: 60px;
        width: 60px;
        padding-top: 20px;
        margin-right: 15px;
        position: relative;
        z-index: 10;
        cursor: pointer;
    }
    .burger {
        height: 5px;
        width: 50px;
        background-color: white;
        position: absolute;
        left: 4px;
        top: 26px;
        transition: all 0.3s ease 0s;
    }
    .burger::before {
        content: "";
        height: 5px;
        width: 50px;
        background-color: white;
        transform: translate(0px, -15px);
        position: absolute;
    }
    .burger::after {
        content: "";
        height: 5px;
        width: 50px;
        background-color: white;
        transform: translate(0px, 15px);
        position: absolute;
    }
    .burger.active {
        top: 50%;
        transform: rotate(45deg) translate( 0px, 0px);
    }
    .burger.active::before {
        top: 50%;
        transform: rotate(-90deg) translate(2px, 0px);

    }
    .burger.active::after {
        transform:scale(0);
    }

    .burger__menu {
        position: absolute;
        display: block;
        height: 100vh;
        width: 100vw;
        background-color: rgba(0, 0, 0, 0.97);
        z-index: 1;
        left: -100vw;
        top: 0;
        transition: left 0.3s ease 0s;
    }
    .burger__menu.active {
        left: 0;
    }

    .burger__list {
        color: white;
        text-align: center;
        padding: 20%;

    }
    .burger__list li {
        color: white;
        font-size: 35px;
        margin-top: 30px;
        border: 1px solid white;
    }
}
